___CSS_LOADER_EXPORT___.push([module.id, `body {
  margin: 0;
  font-family: Arial, sans-serif;
  text-align: center;
}

.base-header {
  background-color: #f9f9f9;
  padding: 2rem 1rem;
  border-bottom: 1px solid #ddd;
}

header h1 {
  margin: 0;
  font-size: 2rem;
}

header a {
  text-decoration: none;
  color: #333;
}

main {
  padding: 2rem;
}

html, body {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  padding: 2rem;
}

footer {
  margin-top: auto;
  font-size: 0.9rem;
  color: #777;
  padding: 1rem;
  background-color: #f0f0f0;
}

/* Hidden form */
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.modal-content .close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 24px;
  font-weight: bold;
  color: #aaa;
  cursor: pointer;
}

.modal-content .close:hover {
  color: #000;
}

@media (max-width: 768px) {
  #filters-section .row > div {
    margin-bottom: 0.75rem;
  }
  #filters-section {
    font-size: 0.95rem;
  }
  #filter-count {
    text-align: center;
    margin-top: 0.5rem;
  }
}
.gift-preview {
  width: 100%;
}

/* Sidebar */
@media (max-width: 768px) {
  #sidebar-container {
    max-width: 700px;
    margin: 0 auto;
  }
  #sidebar {
    width: 100%;
  }
  .toggle-sidebar {
    width: 100%;
  }
}
/* form window */
.modal-content {
  position: relative;
  top: 10%;
  margin: 0 auto;
  max-height: 90vh;
  overflow-y: auto;
}`, "",{"version":3,"sources":["webpack://./src/styles/styles.css"],"names":[],"mappings":"AAAA;EACI,SAAA;EACA,8BAAA;EACA,kBAAA;AACJ;;AAEA;EACI,yBAAA;EACA,kBAAA;EACA,6BAAA;AACJ;;AAEA;EACI,SAAA;EACA,eAAA;AACJ;;AAEA;EACI,qBAAA;EACA,WAAA;AACJ;;AAEA;EACI,aAAA;AACJ;;AAEA;EACI,YAAA;EACA,SAAA;EACA,aAAA;EACA,sBAAA;AACJ;;AAEA;EACI,OAAA;EACA,aAAA;AACJ;;AAEA;EACI,gBAAA;EACA,iBAAA;EACA,WAAA;EACA,aAAA;EACA,yBAAA;AACJ;;AAEA,gBAAA;AACA;EACI,aAAA;EACA,eAAA;EACA,UAAA;EACA,OAAA;EACA,MAAA;EACA,WAAA;EACA,YAAA;EACA,cAAA;EACA,oCAAA;AACJ;;AAEA;EACI,yBAAA;EACA,gBAAA;EACA,aAAA;EACA,sBAAA;EACA,UAAA;AACJ;;AAEA;EACI,WAAA;EACA,YAAA;EACA,eAAA;EACA,iBAAA;AACJ;;AAEA;;EAEI,YAAA;EACA,qBAAA;EACA,eAAA;AACJ;;AAEA;EACE,kBAAA;EACA,SAAA;EACA,WAAA;EACA,eAAA;EACA,iBAAA;EACA,WAAA;EACA,eAAA;AACF;;AAEA;EACE,WAAA;AACF;;AAGA;EACE;IACE,sBAAA;EAAF;EAGA;IACE,kBAAA;EADF;EAIA;IACE,kBAAA;IACA,kBAAA;EAFF;AACF;AAKA;EACE,WAAA;AAHF;;AAOA,YAAA;AACA;EACE;IACE,gBAAA;IACA,cAAA;EAJF;EAOA;IACE,WAAA;EALF;EAQA;IACE,WAAA;EANF;AACF;AASA,gBAAA;AACA;EACI,kBAAA;EACA,QAAA;EACA,cAAA;EACA,gBAAA;EACA,gBAAA;AAPJ","sourcesContent":["body {\n    margin: 0;\n    font-family: Arial, sans-serif;\n    text-align: center;\n}\n\n.base-header {\n    background-color: #f9f9f9;\n    padding: 2rem 1rem;\n    border-bottom: 1px solid #ddd;\n}\n\nheader h1 {\n    margin: 0;\n    font-size: 2rem;\n}\n\nheader a {\n    text-decoration: none;\n    color: #333;\n}\n\nmain {\n    padding: 2rem;\n}\n\nhtml, body {\n    height: 100%;\n    margin: 0;\n    display: flex;\n    flex-direction: column;\n}\n\nmain {\n    flex: 1;\n    padding: 2rem;\n}\n\nfooter {\n    margin-top: auto;\n    font-size: 0.9rem;\n    color: #777;\n    padding: 1rem;\n    background-color: #f0f0f0;\n}\n\n/* Hidden form */\n.modal {\n    display: none;\n    position: fixed;\n    z-index: 1;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    overflow: auto;\n    background-color: rgba(0, 0, 0, 0.4);\n}\n\n.modal-content {\n    background-color: #fefefe;\n    margin: 15%